:root{
    --var-fundoNav:rgb(255,255,255);
    --var-fundoBtns:rgb(200,200,200);
}

#navBar{
    display: flex;
    flex-direction: column;
}

.cima{
    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: var(--var-fundoNav);
}
.cima img{
    height: 200px;
    margin-left: 60px;
}

.btns{
    display: flex;
    margin-right: 40px;
    gap: 20px;

}
.btns div{
    font-size: 50px;
    color: #000;
    transition: color 1s;
}
.btns div:hover{
    cursor: pointer;
    color: #0f0;
}

.btnsCel{
    width: 100px;
    height: 100px;
    border: #000 solid 5px;
    border-radius: 20px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
}
.btnsCel:hover{
    cursor: pointer;
}

.treco{
    position: relative;
    height: 10px;
    width: 80%;
    background-color: #000;
    border-radius: 10px;
}

.options{
    display: flex;
    flex-direction: column;
    background-color: var(--var-fundoNav);
    gap: 10px;
    align-items: center;
}

.option{
    text-align: center;
    font-size: 20px;
    width: 90%;
}
.option:hover{
    cursor: pointer;
}